<template>
  <div class="container">
    <!-- Menu -->
    <div v-if="state=='menu'">
      <!-- Task HITs: {{ backend_url }} -->

      <br>
      <br>

      <!-- <li>File name structure: pilot_{HIT_index}_{annotation_type}_{annotator_idx}_{annotation_task}.csv</li>
      <li>Each HIT_index identifies the same set of 100 sentence pairs to be annotated.</li>
      <li>HITs come in two annotation_types: three-way and two-way, each two-way instance is split into two HITs: twowayA and twowayB.</li>
      <li>The three-way and two-way variants with the same HIT_index should be completed by different annotators since they contain the same sentences.</li>
      <li>The A and B splits of the same two-way instance (e.g. pilot_hit00000_twowayA_annotatorC_fluency.csv and pilot_hit00000_twowayB_annotatorC_fluency.csv ) should be completed by the same annotator.</li>
      <li>Each HIT involves two annotation_tasks: semantic and fluency, which can be completed by different annotations.</li>
      <li>Each HIT should be completed in one sitting. The system only saves the annotations when a HIT is completed.</li> -->
      <li> Please enter your full name in the text box below and click submit to start an annotation task. </li>
      <li> It might take a few seconds for the data to load. Please click the submit button only once. </li>
      <li> Each task contains 100 annotation requests. </li>
      <li> For each annotation request, you will be asked to provide ratings using a slider. You will not be allowed to progress to the next request untill you click on the slider. If you want the slider to stay in the middle, click the slider in the middle and then click next. </li>
      <li> Each task should be completed in one sitting. If you want to take a break while finishing a task, please leave the web page open as the progress is only saved when the entire task is completed. </li>
      <li> Please do not refresh the page while completing a task. Doing so will bring you back to this overview page without saving your progress. </li>
      
      <br>

      <div>
        <p> Enter your full name here. Please make sure it is consistent across submissions.</p>
        <input id="menu_annotator_name" type="text" class="form-control" placeholder="Annotator name" aria-label="Username" aria-describedby="basic-addon1">
          <button  class="btn btn-primary" v-on:click="menu_get_task()"> Submit </button>
      </div>

      <div hidden>
        <P> Select annotator: </P>
        <select class="form-select" id="annotator_filter" aria-label="Default select example">
          <option v-for="(item, index) in annotator_ids" v-bind:key="index" v-bind:value="item" v-on:click="set_filter(item)"> {{ item }} </option>
        </select>
        <button class="btn btn-primary" v-on:click="set_filter_button()"> Apply Filter </button>
      </div>
      <br>
      <br>

      <div v-for="(item, index) in hit_chunks" v-bind:key="index" hidden>
        <li v-if="selected_annotator_id=='All' || selected_annotator_id==item.annotator">
          <p>
            {{ item.display_name }} 
            <span v-if="item.status=='incomplete'"> {{ item.status }} </span>
            <span v-else class="complete_annotations"> {{ item.status }} </span> &nbsp;
            <button class="btn btn-primary" v-on:click="set_state_annotate(item.name)"> Annotate </button>
          </p>
        </li>
      </div>
      
    </div>

    <!-- Annotation view -->
    <div v-if="state=='annotate'">
      <br>
      <p> Completed annotation requests: {{ annotation_idx }} / {{ Object.keys(this.annotation_data).length }} </p>
      <br>
      <p><b>Please read the the following sentences:</b></p>
      <p v-if="annotation_task=='semantic'">
        Reference: <br>
        {{ this.annotation_data[this.annotation_idx].ref }}
      </p>
      <p v-if="annotation_type=='threeway'">
        Sentence 1: <br>
          <span v-if="this.annotation_data[this.annotation_idx].display_order==0">
            <span v-for="(item, index) in this.annotation_data[this.annotation_idx].mt" v-bind:key="index">
              <span v-if="item[1]=='highlighted'" class="highlighted_span">{{ item[0] }} </span>
              <span v-else>{{ item[0] }} </span>
            </span>
          </span>

          <span v-else> 
            <span v-for="(item, index) in this.annotation_data[this.annotation_idx].adv" v-bind:key="index">
              <span v-if="item[1]=='highlighted'" class="highlighted_span">{{ item[0] }} </span>
              <span v-else>{{ item[0] }} </span>
            </span>
          </span>
      </p>

      <p v-if="annotation_type=='twoway'">
        Sentence 1: <br>
          <span v-if="this.annotation_data[this.annotation_idx].to_eval=='mt'">
            <span v-for="(item, index) in this.annotation_data[this.annotation_idx].mt" v-bind:key="index">
              <span v-if="item[1]=='highlighted'" class="highlighted_span">{{ item[0] }} </span>
              <span v-else>{{ item[0] }} </span>
            </span>
          </span>

          <span v-else> 
            <span v-for="(item, index) in this.annotation_data[this.annotation_idx].adv" v-bind:key="index">
              <span v-if="item[1]=='highlighted'" class="highlighted_span">{{ item[0] }} </span>
              <span v-else>{{ item[0] }} </span>
            </span>
          </span>
      </p>


      <p v-if="annotation_type=='threeway'">
        Sentence 2: <br>
        <span v-if="this.annotation_data[this.annotation_idx].display_order==0">
          <span v-for="(item, index) in this.annotation_data[this.annotation_idx].adv" v-bind:key="index">
            <span v-if="item[1]=='highlighted'" class="highlighted_span">{{ item[0] }} </span>
            <span v-else>{{ item[0] }} </span>
          </span>
        </span>
        <span v-else>
          <span v-for="(item, index) in this.annotation_data[this.annotation_idx].mt" v-bind:key="index">
            <span v-if="item[1]=='highlighted'" class="highlighted_span">{{ item[0] }} </span>
            <span v-else>{{ item[0] }} </span>
          </span>
        </span>
      </p>
      
      <br><br>

      <p v-if="annotation_type=='threeway'">
        <b>Taking the highlighted words into consideration, rate the above sentences by how much you agree that:</b>
      </p>
      <p v-else>
        <b>Taking the highlighted words into consideration, rate the above sentence by how much you agree that:</b>
      </p>
      <p v-if="annotation_task=='semantic'">
        The meaning of the reference sentence is adequately expressed in Sentence 1.
      </p>
      <p v-else>
        Sentence 1 is fluent English.
      </p>

      <div class="slidecontainer">
        <p style="text-align:left;">
          Strongle disagree
          <span style="float:right;">
            Strongle agree
          </span>
        </p>
        <fieldset class="range__field">
          <svg role="presentation" width="100%" height="20" xmlns="http://www.w3.org/2000/svg">
              <rect class="range__tick" x="25%" y="3" width="1" height="20"></rect>
              <rect class="range__tick" x="50%" y="3" width="1" height="20"></rect>
              <rect class="range__tick" x="75%" y="3" width="1" height="20"></rect>
          </svg>
          <input class="slider" id="slider_1" type="range" min="0" max="1000" value="500" v-on:click="set_slider_1_moved()">
          <svg class="svg_below" role="presentation" width="100%" height="20" xmlns="http://www.w3.org/2000/svg">
              <rect class="range__tick" x="25%" y="3" width="1" height="20"></rect>
              <rect class="range__tick" x="50%" y="3" width="1" height="20"></rect>
              <rect class="range__tick" x="75%" y="3" width="1" height="20"></rect>
          </svg>
      </fieldset>
      </div>

      <div v-if="annotation_type=='threeway'">
        <p v-if="annotation_task=='semantic'">
          The meaning of the reference sentence is adequately expressed in Sentence 2.
        </p>
        <p v-else>
          Sentence 2 is fluent English.
        </p>

        <div class="slidecontainer">
          <p style="text-align:left;">
            Strongle disagree
            <span style="float:right;">
              Strongle agree
            </span>
          </p>
          <fieldset class="range__field">
            <svg role="presentation" width="100%" height="20" xmlns="http://www.w3.org/2000/svg">
                <rect class="range__tick" x="25%" y="3" width="1" height="20"></rect>
                <rect class="range__tick" x="50%" y="3" width="1" height="20"></rect>
                <rect class="range__tick" x="75%" y="3" width="1" height="20"></rect>
            </svg>
            <input class="slider" id="slider_2" type="range" min="0" max="1000" value="500" v-on:click="set_slider_2_moved()">
            <svg class="svg_below" role="presentation" width="100%" height="20" xmlns="http://www.w3.org/2000/svg">
                <rect class="range__tick" x="25%" y="3" width="1" height="20"></rect>
                <rect class="range__tick" x="50%" y="3" width="1" height="20"></rect>
                <rect class="range__tick" x="75%" y="3" width="1" height="20"></rect>
            </svg>
          </fieldset>
          </div>
        </div>

        <div v-if="next_available">
          <button v-if="(annotation_scores.length < Object.keys(this.annotation_data).length - 1 && annotation_type == 'twoway') || (annotation_scores.length < 2 * Object.keys(this.annotation_data).length - 2 && annotation_type == 'threeway')" class="btn btn-primary" v-on:click="next_annotation()"> Next </button>
          <div v-else>
            <input v-on:click="update_annotator()" id="annotator" type="text" class="form-control" placeholder="Input your full name here. Please make sure it is consistent across submissions." aria-label="Username" aria-describedby="basic-addon1">
            <button v-if="annotator_entered" class="btn btn-success" v-on:click="submit_annotation()"> Submit </button>
            <button v-else class="btn btn-primary" disabled> Submit </button>
          </div>
        </div>
        <div v-else>
          <button class="btn btn-primary" disabled> Next </button>
        </div>
      </div>

      </div>
    
</template>

<script>
const axios = require('axios').default

export default {
  name: 'Menu',
  data () {
    return {
      // Constants
      backend_url: '', //'http://127.0.0.1:5000',

      // States
      state:'menu',
      annotation_type: 'threeway',
      annotation_task: 'semantic',
      slider_1_moved: false,
      slider_2_moved: false,
      next_available: false,
      annotator_entered: false,

      // Data
      file_name: 0,
      hit_chunks: [],
      annotation_data: [],
      annotation_idx: 0,
      annotation_scores: [],

      annotator_ids: ['All'],
      selected_annotator_id: 'All',
    }
  },
  computed: {
  },
  methods: {
    // Fetch data from the server
    get_menu_data(){
      axios.get(this.backend_url + '/get_menu_data')
        .then(response => {
          this.hit_chunks = response.data.chunks
          this.annotator_ids = ['All']
          this.set_filter(this.selected_annotator_id)
          for (let i = 0; i < this.hit_chunks.length; i++){
            let annotator_id = this.hit_chunks[i].annotator
            if (!this.annotator_ids.includes(annotator_id)){
              this.annotator_ids.push(annotator_id)
            }
          }
        })
    },

    // Filter annotators
    set_filter(annotator_id){
      this.selected_annotator_id = annotator_id
      document.getElementById('annotator_filter').value = annotator_id
    },

    set_filter_button(){
      this.selected_annotator_id = document.getElementById('annotator_filter').value
    },

    // Managing states
    set_state_annotate(file_name){
      this.slider_1_moved = false
      this.slider_2_moved = false
      this.update_next_status()
      this.state = 'annotate'
      if (file_name.includes('threeway')){
        this.annotation_type = 'threeway'
      } else {
        this.annotation_type = 'twoway'
        this.slider_2_moved = true
      }
      if (file_name.includes('semantic')){
        this.annotation_task = 'semantic'
      } else {
        this.annotation_task = 'fluency'
      }

      // Initialise the indices and annotations
      this.annotation_idx = 0
      this.annotation_scores = []
      
      axios.get(this.backend_url + '/get_csv_data?file_name=' + file_name)
        .then(response => {
          this.file_name = file_name
          this.annotation_idx = 0
          this.annotation_data = JSON.parse(JSON.stringify(response.data))
        })
    },

    // Navigate the annotation interface
    set_slider_1_moved(){
      this.slider_1_moved = true
      this.update_next_status()
    },
    set_slider_2_moved(){
      this.slider_2_moved = true
      this.update_next_status()
    },
    update_next_status(){
        this.next_available = false
      if (this.slider_1_moved && this.slider_2_moved){
        this.next_available = true
      }
    },

    next_annotation(){
      this.slider_1_moved = false
      this.slider_2_moved = false
      this.update_next_status()
      if (this.annotation_type == 'threeway'){
        this.annotation_scores.push(document.getElementById('slider_1').value)
        this.annotation_scores.push(document.getElementById('slider_2').value)
        document.getElementById('slider_2').value = 500
      }
      else{
        this.slider_2_moved = true
        this.annotation_scores.push(document.getElementById('slider_1').value)
      }
      this.annotation_idx += 1
      document.getElementById('slider_1').value = 500
      console.log(this.annotation_scores)
      },

    update_annotator(){
      this.annotator_entered = true
    },

    submit_annotation(){
      // Add the last annotation
      this.slider_1_moved = false
      this.slider_2_moved = false
      this.annotator_entered = false
      this.update_next_status()
      if (this.annotation_type == 'threeway'){
        this.annotation_scores.push(document.getElementById('slider_1').value)
        this.annotation_scores.push(document.getElementById('slider_2').value)
        document.getElementById('slider_2').value = 500
      }
      else{
        this.slider_2_moved = true
        this.annotation_scores.push(document.getElementById('slider_1').value)
      }
      document.getElementById('slider_1').value = 500
      console.log(this.annotation_scores)
      let annotator = document.getElementById('annotator').value
      axios.get(this.backend_url + '/send_annotations?file_name=' + this.file_name + '&annotation_scores=' + JSON.stringify(this.annotation_scores) + '&annotator=' + annotator)
        .then(response => {
          console.log(response)
          this.get_menu_data()
          this.state = 'menu'
        })
    },

    menu_get_task(){
      this.slider_1_moved = false
      this.slider_2_moved = false
      this.update_next_status()
      this.state = 'annotate'

      // Initialise the indices and annotations
      this.annotation_idx = 0
      this.annotation_scores = []

      let annotator = document.getElementById('menu_annotator_name').value
      axios.get(this.backend_url + '/dispatch_job?annotator=' + annotator)
        .then(response => {
          let received = JSON.parse(JSON.stringify(response.data))
          let file_name = received.file_name
          this.file_name = received.file_name
          this.annotation_idx = 0
          this.annotation_data = received.data

          console.log(file_name)

          if (file_name.includes('threeway')){
            this.annotation_type = 'threeway'
          } else {
            this.annotation_type = 'twoway'
            this.slider_2_moved = true
          }
          if (file_name.includes('semantic')){
            this.annotation_task = 'semantic'
          } else {
            this.annotation_task = 'fluency'
          }

        })
    },
  },
  mounted(){
    // this.get_menu_data()
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
}

/* Slider */
.slider {
  width: 100%; /* Full-width */
  padding-top: 0px;
  margin-top: 0px;
  padding-bottom: 0px;
  margin-top: 0px;
}

.svg_below {
  position: relative;
  bottom: 13px;
}

/* Highlight */
.highlighted_span {
  background-color: cyan;
}
.complete_annotations {
  background-color: rgb(45, 184, 59);
}
</style>
